import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Switch, Route, Link } from "react-router-dom";
import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import "bootstrap/dist/js/bootstrap.min.js";
import Home from "./components/Home/Home";
import About from "./components/About/About";
import Projects from "./components/Projects/Projects";
import Blog from "./components/Blog/Blog";
import Loader from "react-loader-spinner";

function App() {
  const [section, setSection] = useState("home");
  const [isLoading, setIsLoading] = useState(true);
  useEffect(() => {
    let timeOut = setTimeout(() => {
      setIsLoading(false);
    }, 2000);
    return () => {
      clearTimeout(timeOut);
    };
  });
  if (isLoading === true) {
    return (
      <div id="loader">
        <Loader
          type="Puff"
          color="#111"
          height={400}
          width={400}
          timeout={2000} //2 secs
        />
      </div>
    );
  }
  return (
    <div className="App">
      <Router>
        <nav className="navbar navbar-expand-md navbar-light bg-white py-4">
          <div className="container">
            <Link
              className="navbar-brand"
              to="/"
              onClick={() => {
                setSection("home");
                setIsLoading(true);
              }}
            >
              <span id="header-font">Pankaj Singh</span>
            </Link>
            <button
              className="navbar-toggler"
              type="button"
              data-bs-toggle="collapse"
              data-bs-target="#navbarSupportedContent"
              aria-controls="navbarSupportedContent"
              aria-expanded="false"
              aria-label="Toggle navigation"
            >
              <span className="navbar-toggler-icon"></span>
            </button>
            <div
              className="collapse navbar-collapse justify-content-end"
              id="navbarSupportedContent"
            >
              <ul className="navbar-nav fs-4 fw-bold">
                <li className="nav-item">
                  <Link
                    className={`nav-link ${section === "home" ? "active" : ""}`}
                    to="/"
                    onClick={() => {
                      setSection("home");
                    }}
                  >
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className={`nav-link ${
                      section === "about" ? "active" : ""
                    }`}
                    to="/about"
                    onClick={() => {
                      setSection("about");
                    }}
                  >
                    About
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className={`nav-link ${section === "blog" ? "active" : ""}`}
                    to="/blog"
                    onClick={() => {
                      setSection("blog");
                    }}
                  >
                    Blog
                  </Link>
                </li>
                <li className="nav-item">
                  <Link
                    className={`nav-link ${
                      section === "projects" ? "active" : ""
                    }`}
                    to="/projects"
                    onClick={() => {
                      setSection("projects");
                    }}
                  >
                    Projects
                  </Link>
                </li>
              </ul>
            </div>
          </div>
        </nav>
        <div className="main-container">
          <Switch>
            <Route exact path="/">
              <Home />
            </Route>
            <Route path="/about">
              <About />
            </Route>
            <Route path="/projects">
              <Projects />
            </Route>
            <Route path="/blog">
              <Blog />
            </Route>
            <Route path="*">
              <h1>Not found - 404</h1>
              <Link to="/">Back to Home</Link>
            </Route>
          </Switch>
        </div>
      </Router>
    </div>
  );
}

export default App;
